@import url(https://fonts.googleapis.com/css2?family=Ubuntu:wght@500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Ubuntu:wght@500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* Header styling starts here */
  .header span{
    color: white;
    font-size: 30px;
    font-family: 'Ubuntu', sans-serif;
    padding: 12px;
    align-items: center;
    display: flex;
  }

  /* SearchBar styling starts here */
  .body-container{
    display: flex;
    justify-content: center;
    padding-top: 10px;
    align-items: center;
    height: 11vh;
  }
  .body-container input{
    width: 550px;
    padding-left: 5px;
    height: 40px;
    font-size: 22px;
    border: none;
    color: gray;
    box-shadow: 2px 5px 8px 4px rgb(216, 216, 216);
  }
  .body-container button{
    color: white;
    font-size: 25px;
    background: #6500f2;
    width: 100px;
    height: 45px;
    cursor: pointer;
    border: rgb(216, 216, 216) solid 2px;
    box-shadow: 2px 5px 8px 4px rgb(216, 216, 216);
  }
  .body-container button:hover{
    opacity: 0.7;
  }
  
  
/* Responsive UI stying starts here */
  @media (max-width:1204px){
    .body-container input{
      width: 500px;
    }
  }
  @media (max-width:950px){
    .body-container input{
      width: 400px;
    }
  }
  @media (max-width:788px){
    .body-container input{
      width: 320px;
    }
    .body-container button{
      width: 80px;
      font-size: 20px;
    }
  }
  @media (max-width:588px){
    .body-container input{
      width: 270px;
    }
    .body-container button{
      width: 75px;
      font-size: 17px;
    }
  }
.header{
    background: #e5e1e9;
    height: 60px;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .nav-items{
      list-style: none;
      display: flex;
    align-items:center;
    font-size: 18px;
    font-weight: 600;  
  }
  .header .nav-items li{
      padding: 0 30px;
  }
  .header .nav-items li:hover{
    color: rgb(140, 83, 194);
    cursor: pointer;
}

@media (max-width:500px){
  .header .nav-items li{
    padding: 0 15px;
  }
}
@media (max-width:400px){
  .header .nav-items li{
    padding: 0 10px;
  }
}
/* loading box styling starts here */
.status-box{
    display: flex;
    justify-content: center; 
    font-family: 'Ubuntu', sans-serif;
}
.status-box p{
    background: white;
    padding-left: 10px;
    display: flex;
    align-items: center;
    width: 60%;
    margin-top: 0;
    height: 14vh;
    box-shadow: 2px 5px 8px 4px rgb(209, 209, 209);
    font-size: 19px;
    color: rgb(110, 110, 110);
}
.status-box .error{
    justify-content: center;
    margin-left: 0;
}

/* Fetched weather data styling starts here */
.weather-container{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.weather-info{
    font-family: 'Ubuntu', sans-serif;
    background: white;
    width: 65%;
    padding-bottom: 20px;
    box-shadow: 2px 5px 8px 4px rgb(209, 209, 209);
    font-size: 19px;
    color: rgb(94, 94, 94);
    display: flex;
    justify-content: space-between;
}
.weather-info .left-container div{
    margin: 10px 0 0 10px;
}
.weather-info .left-container .temp{
    font-size: 80px;
    color: rgb(63, 63, 63);
    font-weight: 1000;
    display: flex;
    margin-top: 70px;
    margin-left: 50px;
    align-items: center;
}
.weather-info .left-container .temp img{
    font-size: 20px;
    width: 80px;
    height: 80px;
}
.weather-info ul{
    list-style: none;
    margin: 27px 170px 0 0;
}
.weather-info ul .divider{
    padding: 10px;
}
.weather-info ul .divider span{
    font-weight: bolder;
    color: rgb(82, 82, 82);
}

/* Responsive UI stying starts here */
@media (max-width:1204px){
    .weather-info{
        width: 70%;
    }
    .weather-info ul{
        margin-right: 120px;
    }
}
@media (max-width:1050px){
    .weather-info{
        width: 75%;
        font-size: 16px;
    }
    .status-box p{
        width: 75%;
    }
    .weather-info .left-container .temp{
        font-size: 60px;
    }
    .weather-info .left-container .temp img{
        width: 50px;
        height: 50px;
    }
    .weather-info ul{
        margin-right: 100px;
    }
}
@media (max-width:788px){
    .weather-info{
        width: 80%;
    }
    .weather-info .left-container .temp{
        font-size: 45px;
        margin-left: 35px;
    }
    .weather-info ul{
        margin-right: 60px;
    }
}
@media (max-width:588px){
    .weather-info{
        width: 90%;
        font-size: 13px;
    }
    .status-box p{
        width: 90%;
    }
    .weather-info .left-container .temp{
        font-size: 35px;
        margin-left: 25px;
        margin-top: 80px;
    }
    .weather-info ul{
        margin-right: 45px;
    }
}
@media (max-width:445px){
    .weather-info{
        width: 100%;
        font-size: 12.5px;
    }
    .weather-info .left-container .temp{
        margin-left: 15px;
        margin-top: 80px;
    }
    .weather-info .left-container .temp img{
        width: 30px;
        height: 30px;
    }
    .weather-info ul{
        margin-right: 35px;
    }
}
.main-heading{
    font-size: 30px;
    text-align: center;
    font-weight: 900;
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    color: rgb(66, 66, 66);
    margin: 10px 0;
}
.data-container{
    margin: 20px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    
}
.app{
    display: flex;
    height: 80vh;
    /* flex-direction: column;
    align-items: center;
    justify-content: center; */
}
.form-group{
    padding: 15px 0;
    font-weight: 700;
    font-size: 18px;
}
.form-control{
    margin: 10px;
    width: 60vh;
    background-color:rgb(220, 233, 233);
    border: none;
    height: 5vh;
}
.form-controls{
    margin: 10px;
    width: 60vh;
    background-color:rgb(220, 233, 233);
    border: none;
}
.btn{
    padding: 10px 6px;
    font-size: 16px;
    width: 100px;
    font-weight: 700;
    background-color: aliceblue;
    color: rgb(59, 59, 59);
    cursor: pointer;
}
@media (max-width:500px){
    .form-control{
        width: 45vh;
    }
    .form-controls{
        width: 45vh;
    }
    .main-heading{
        font-size: 25px;
    }
  }
@media (max-width:400px){
    .form-control{
        width: 40vh;
    }
    .form-controls{
        width: 40vh;
    }
  }

